.login__login-button {
  background-color: #fff;
  color: #000;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  border: 0;
  width: 100%;
  height: 50px;
  transition: opacity .25s;
  outline: none;
  border-radius: 2px;
}

.login__login-button:hover {
  opacity: 0.8;
  cursor: pointer;
}

.login__login-button_inactive {
  border: 1px solid #000;
  opacity: 0.2;
  background-color: #fff;
  color: #000;
  cursor: default;
}

.login__login-button_inactive:hover {
  opacity: 0.2;
  cursor: default;
}