.login__input {
  width: 100%;
  outline: none;
  border: none;
  font-size: 14px;
  line-height: 17px;
  padding: 5px 0 5px 0;
  margin-bottom: 5px;
  border-bottom: solid 2px #fff;
  color: #fff;
  background-color: #000;
  position: relative;
}

.login__input::placeholder {
  color: #c4c4c4;
}

.login__input:focus {
  border: solid 1px rgba(0, 0, 0, 0.2);
}